"use strict";

var handlebars = require("handlebars"),
    template = require("../../../../pub/views/modules/push-down/ui.js");

var body = document.body;
var pushdown;
var pushdownContainer;
var descButtonContainer;
var learnMoreButton;
var gotItButton;

var colorPairs = [
    // green , purple
    ["#7acb00","#6e27c5"],
    // pink , blue
    ["#ff0198","#150f96"],
    // purple , reddish
    ["#6e27c5","#f1352b"],
    // orange , light blue
    ["#ff6600","#01c1d6"]
];

var randomColorPair = colorPairs[Math.floor(Math.random() * colorPairs.length)];

function show(data, onUserAccept) {
    body.insertAdjacentHTML("afterbegin", template(data));

    pushdownContainer = document.querySelector("#pushdown-container");
    pushdown = pushdownContainer.querySelector(".pushdown");    
    descButtonContainer = pushdown.querySelector(".desc-buttons");
    learnMoreButton = pushdownContainer.querySelector(".learn-more");
    gotItButton = pushdownContainer.querySelector(".got-it");

    pushdownContainer.className += "pushdown-slide-down";

    // change colors of background and forground and "learn more" text
    pushdown.style.backgroundColor = randomColorPair[0];
    descButtonContainer.style.backgroundColor = randomColorPair[1];
    learnMoreButton.style.color = randomColorPair[0];

    // add event listeners to ctas
    gotItButton.addEventListener("click", function(e){
        e.preventDefault();
        console.log('clicked got it');
        onUserAccept('GOT_IT');
    });

    learnMoreButton.addEventListener("click", function(e){
        console.log('clicked learn more');
        onUserAccept();
    });
}

function showCompact(data, onUserAccept) {
    body.insertAdjacentHTML("afterbegin", template(data));

    pushdownContainer = document.querySelector("#pushdown-container");
    pushdown = pushdownContainer.querySelector(".pushdown");
    descButtonContainer = pushdown.querySelector(".desc-buttons");
    learnMoreButton = pushdownContainer.querySelector(".learn-more");
    gotItButton = pushdownContainer.querySelector(".got-it");

    pushdownContainer.className += "pushdown-slide-down";
    pushdown.className += " pushdown-compact";

    // change colors of background and forground and "learn more" text
    pushdown.style.backgroundColor = randomColorPair[0];
    descButtonContainer.style.backgroundColor = randomColorPair[1];
    learnMoreButton.style.color = randomColorPair[0];

    // add event listeners to ctas
    gotItButton.addEventListener("click", function(e){
        e.preventDefault();
        console.log('clicked got it');
        onUserAccept('GOT_IT');
    });

    learnMoreButton.addEventListener("click", function(e){
        console.log('clicked learn more');
        onUserAccept();
    });

}


function hide(){
    pushdownContainer.className += " pushdown-slide-up";
}

function compact(){
    pushdown.className += " pushdown-compact";
}

module.exports = {
    show: show,
    showCompact: showCompact,
    hide: hide,
    compact: compact
};

