"use strict";

var cookies = require("js-cookie"),
    api = require("./api"),
    ui = require("./ui");

var data;
var pushdownContainer = document.querySelector("#pushdown-container");
var isCompact = window.pushdownConfig.isCompact || false;
var pushDownType = isCompact ? "compact" : "standard";
var localData;

function setCookie(){
    var expireTime = 7; //days
    console.log(localData.contentKey);
    var cookieValue = 'content' + localData.contentKey;
    cookies.set('pushdown', cookieValue, { expires: expireTime });
}

function setCache(data){
    console.log('set session storage!');
    sessionStorage.setItem("pushdown", JSON.stringify(data));
}

function onUserAccept(type){
    //add the cookie here
    setCookie();
    if(type === 'GOT_IT'){
        if(pushDownType === "compact"){
            ui.compact();
        } else {
            ui.hide();
        }
    }
}

function checkCookie(data) {
    var cookieValue = 'content' + data.contentKey;

    //do cookie check and subsequent logic.
    if(cookieValue !== cookies.get('pushdown')){
        console.log('show normal pushdown');
        //clear pushdown cookie
        cookies.remove('pushdown');
        ui.show(data, onUserAccept);
    } else {
        if (pushDownType === "compact") {
            console.log('show compact pushdown');
            ui.showCompact(data, onUserAccept);
        } else {
            ui.hide();
        }
    }
}

function onAjaxComplete(def){

    def.then(function (data) {
        checkCookie(data);
        localData = data;        
        setCache(data);
    });
}


function init() {
    console.log("start push down controller component");
    //startAjax
    if (sessionStorage.getItem("pushdown")) {
        console.log('data is stored in session');
        localData = JSON.parse(sessionStorage.getItem("pushdown"));
        checkCookie(localData);
    } else {
        onAjaxComplete(api.getContent());
    }
}




module.exports = {
    init: init
};
