"use strict";

var reqwest = require("reqwest"),
    q = require("q");

function getContent() {

    console.log("getting content from api");

    var useJSONP = !!window.pushdownConfig.endPoint;

    function stubbDef() {

        var data = {
            "header": "FOOO ACE and Chubb are now one",
            "description": "ACE has acquired Chubb, creating a global insurance leader that will operate under the renowned Chubb name. Learn more about this historic combination here.",
            "header-compact": "Ace and Chubb are now one and operating under the renowned Chubb name.",
            "header-mobile": "Ace and Chubb are now one.",
            "ctaOne": "Not now",
            "ctaTwo": "Learn more",
            "contentKey": 1
        };

        var def = q(data);

        return def;
    }

    var r = reqwest({
        url: window.pushdownConfig.endPoint,
        type: 'jsonp',
        success: function (resp) {
            console.log("the jsonp resp:", resp);
        }
    });

    return useJSONP ? r : stubbDef();

}

module.exports = {
    getContent: getContent
};
